import styled from 'styled-components';
import { color, size, mixin } from '@styles';
import { Link } from 'gatsby';

export const HeaderParagraph = styled(Link)`
  color: white;
  padding: 0 1rem;
  text-decoration: none;
  text-align: center;
  ${mixin.ellipsisOneLine}
`;

export const HeaderSideBox = styled.div`
  ${mixin.flexMiddle}
  position: relative;

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    width: 80%;
    height: 80%;
    border-radius: 50%;
  }

  @media (hover: hover) {
    &:hover {
      &::before {
        background-color: ${color.SECONDARY};
      }
    }
  }
`;

export const HeaderLayout = styled.div`
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: ${size.HEADER_HEIGHT};
  background-color: ${color.PRIMARY};
  z-index: ${size.HEADER_Z_INDEX};
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  ${HeaderParagraph} {
    display: block;
    width: 100%;
  }

  ${HeaderSideBox} {
    flex: 0 0 ${size.HEADER_HEIGHT};
    aspect-ratio: 1/1;
  }
`;

export const HeaderProfileBox = styled.div`
  a {
    ${mixin.flexMiddle}

    img {
      width: 50%;
      border-radius: 50%;
    }
  }
`;
