import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'gatsby';
import MenuButton from '@components/MenuButton';
import Sidebar from '@components/Sidebar';
import type { TitleState } from '@store/modules/title';
import type { RootState } from '@store/index';
import {
  HeaderLayout,
  HeaderSideBox,
  HeaderParagraph,
  HeaderProfileBox,
} from './Styles';

interface Props {
  profileUrl?: string;
}

function Header({ profileUrl }: Props) {
  const { title } = useSelector<RootState, TitleState>((state) => state.title);

  const [isMenuOpen, setMenuOpen] = useState(false);

  return (
    <HeaderLayout>
      <Sidebar isOpen={isMenuOpen} setIsOpen={setMenuOpen} />

      <HeaderSideBox>
        <MenuButton isOpen={isMenuOpen} setIsOpen={setMenuOpen} />
      </HeaderSideBox>

      <HeaderParagraph to="/">{title}</HeaderParagraph>

      <HeaderSideBox>
        <HeaderProfileBox>
          {profileUrl && (
            <Link to="/about">
              <img src={profileUrl} />
            </Link>
          )}
        </HeaderProfileBox>
      </HeaderSideBox>
    </HeaderLayout>
  );
}

export default Header;
