import React from 'react';
import { Helmet } from 'react-helmet';

interface Props {
  userName: string;
  repoName: string;
  googleSiteVerification?: string;
}

function Seo({ userName, repoName, googleSiteVerification }: Props) {
  const meta = [];

  if (googleSiteVerification) {
    meta.push({
      name: 'google-site-verification',
      content: googleSiteVerification,
    });
  }

  return <Helmet meta={meta} title={`${repoName} (${userName}'s repo)`} />;
}

export default Seo;
